import assert from "assert";
import PointReducer from "../PointReducer.js";

exports["Point Reducer: Linear"] = () => {
  const pointReducer = new PointReducer([0, 100]);
  const value = pointReducer.valueAt(0.5);

  assert.equal(value, 50);
};

exports["Point Reducer: EaseIn"] = () => {
  const pointReducer = new PointReducer([0, 0, 100]);
  let value = pointReducer.valueAt(0.5);

  assert.equal(value, 25);

  value = pointReducer.valueAt(0.75);
  assert.equal(value, 56.25);

  value = pointReducer.valueAt(1);
  assert.equal(value, 100);

};
